<template>
   <div>
      <div v-if="!myData">
         <content-loader
            :animate="true"
            :height="500"
            :width="350"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            v-if="ResizeScreen == 1"
            style="margin-top:80px;"
         >
            <rect x="10" y="0" rx="0" ry="0" width="330" height="200" />
            <rect x="35" y="220" rx="0" ry="0" width="280" height="20" />
            <rect x="55" y="250" rx="0" ry="0" width="230" height="10" />
            <rect x="55" y="270" rx="0" ry="0" width="230" height="10" />
            <rect x="10" y="300" rx="0" ry="0" width="330" height="100" />
            <rect x="10" y="420" rx="0" ry="0" width="330" height="100" />
            
         </content-loader>

         <content-loader
            :animate="true"
            :height="600"
            :width="600"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            v-if="ResizeScreen == 2"
            style="margin-top:80px;"
         >
            <rect x="10" y="0" rx="0" ry="0" width="200" height="200" />
            <rect x="35" y="220" rx="0" ry="0" width="150" height="20" />
            <rect x="55" y="250" rx="0" ry="0" width="110" height="10" />
            <rect x="55" y="270" rx="0" ry="0" width="110" height="10" />
            <rect x="10" y="290" rx="0" ry="0" width="200" height="200" />
            <rect x="10" y="500" rx="0" ry="0" width="200" height="100" />
            
            <rect x="220" y="0" rx="0" ry="0" width="365" height="600" />
            
         </content-loader>

         <content-loader
            :animate="true"
            :height="290"
            :width="600"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            v-if="ResizeScreen == 3"
         >
            <rect x="0" y="0" rx="0" ry="0" width="150" height="130" />
            <rect x="23" y="140" rx="0" ry="0" width="100" height="15" />
            <rect x="32" y="160" rx="0" ry="0" width="80" height="15" />
            <rect x="15" y="190" rx="0" ry="0" width="50" height="20" />
            <rect x="80" y="190" rx="0" ry="0" width="50" height="20" />
            <rect x="0" y="220" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="230" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="240" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="250" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="260" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="270" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="280" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="290" rx="0" ry="0" width="150" height="5" />

           <rect x="180" y="0" rx="0" ry="0" width="450" height="190" />
           <rect x="180" y="220" rx="0" ry="0" width="450" height="75" />
         </content-loader>
      </div>

      <div class="BXviewcoach" v-if="myData">
         <div class="BXviewcoach__BXcard" >
            <CardProfile :Get_Profile="Get_Profile"
            />
         </div>
         <div class="BXviewcoach__BXInfoStudent">
           <CoachNote 
            :DataSession = 'DataSession'
           />
           <StudentProgress />
         </div>
      </div>
   </div>
   
</template>

<script>
   import moment from 'moment'
   import { ContentLoader } from "vue-content-loader"
   import CardProfile from '../../coach-components/coach-profile-view/view-profile-student/cardProfile/CardProfile.vue';
   import CoachNote from '../../coach-components/coach-profile-view/view-profile-student/coachNote/CoachNote.vue';
   import StudentProgress from '../../coach-components/coach-profile-view/view-profile-student/studentProgress/StudentProgress.vue';
   export default {
      components:{
         CardProfile,
         CoachNote,
         StudentProgress,
         ContentLoader
      },
      data() {
         return {
            myData :null,
            drawer: null,
            windowWidth:0,
            windowHeigt:0,
            ResizeScreen: null
         }
      },
      mounted () {
         const Username = this.$store.state.UpcomingSelect.Username
         this.$store.dispatch("GET_PROFILE_OTHERS", Username);
         this.$store.dispatch("GET_COACH_SEE_STUDENT_NOTE", Username);
         const studentUsername = {
            username: Username
         }
         this.$store.dispatch('GET_CALL1_CALL2', studentUsername)

         setTimeout(() => {
          this.myData = 'Example Data';
         }, 2000)

         this.$nextTick( () => {
            window.addEventListener('resize', this.getWindowWidth);
            window.addEventListener('resize', this.getWindowHeight);

            this.getWindowWidth()
            this.getWindowHeight()
         })

      },
      computed: {
         Get_Profile(){
            return this.$store.state.OtherProfiles ? this.$store.state.OtherProfiles.data : null
         },
         GET_SessionHistory(){
            return this.$store.state.CoachSeeStudentNote? this.$store.state.CoachSeeStudentNote.data : null
         },
         DataSession() {
            const items = []
            const dataValue = this.GET_SessionHistory

            dataValue.forEach(function (value, key) {
               moment(value.date).format('MMMM, Do YYYY')
               items.push(dataValue);
               // console.log(key +" " + value.date+ " " + value.start_time);
            });

            function compare(a, b){
               if (a.date > b.date) return -1;
               if (a.date < b.date) return 1;
               if (a.start_time < b.start_time) return -1;
               if (a.start_time > b.start_time) return 1;
            }

            return [...dataValue].sort(compare)
         },
      },
      methods: {
         getWindowWidth(event){ 
            this.windowWidth = document.documentElement.clientWidth;

            if (this.windowWidth <= 599){
            //this for resolution phone only
            this.ResizeScreen = 1
            }else if(this.windowWidth <= 768){
               //this for resolution Ipad Potrait
            this.ResizeScreen = 2
            }else if(this.windowWidth >= 1024){
               //this for resolution Dekstop-up
            this.ResizeScreen = 3
            }
         },

         getWindowHeight(event){
            this.windowHeigt = document.documentElement.clientHeight;
         }
      },
      beforeDestroy() {
         window.removeEventListener('resize', this.getWindowWidth);
         window.removeEventListener('resize', this.getWindowHeight);
      },
   }
</script>

<style lang="scss">
   @import '../../styles/StudentProfileView.scss';
</style>


